class AppMenu extends PureComponent {

  handleMouseEnter = (e) => {
    e.target.style.color = '#ffc414';
  };

  handleMouseLeave = (e) => {
    e.target.style.color = 'white';
  };

  render() {
    return (
      <div id="iconGroup">
        <div id="appMenu" >
          <a className="homeMenu" href="https://www.youtube.com/channel/UCcC5mGUWbGbrbcc077AXVvw?view_as=subscriber">
            <span><i className="fa fa-youtube-play" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}></i></span>
          </a>
          <a className="homeMenu" href="https://soundcloud.com/indazoo">
            <span><i className="fa fa-soundcloud" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}></i></span>
          </a>
          <a className="homeMenu" href="https://www.instagram.com/indazoo_official">
            <span><i className="fa fa-instagram" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}></i></span>
          </a>
          <a className="homeMenu" href="https://web.facebook.com/Ojerime/">
            <span><i className="fa fa-facebook-official" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}></i></span>
          </a>
          <NavLink className="homeMenu" onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave} exact to="/about" >ENTER</NavLink>
          <a className="homeMenu" href="https://smartstore.naver.com/indazoo">
            <span onMouseEnter={this.handleMouseEnter} onMouseLeave={this.handleMouseLeave}>SHOP</span>
          </a>
        </div>
      </div>
    )
  }
}

class AppArrow extends PureComponent {

  state = {
    arrowClicked: false,
    arrowMouseEnter: false,
    closeMouseEnter: false
  }

  _handleArrowClick = () => {
    this.setState({
      arrowClicked: !this.state.arrowClicked,
      arrowMouseEnter: false,
      closeMouseEnter: false
    });
  }

  _handleArrowMouseEnter = () => {
    this.setState({
      arrowMouseEnter: true
    });
  }

  _handleArrowMouseLeave = () => {
    this.setState({
      arrowMouseEnter: false
    });
  }

  _handleCloseMouseEnter = () => {
    this.setState({
      closeMouseEnter: true
    });
  }

  _handleCloseMouseLeave = () => {
    this.setState({
      closeMouseEnter: false
    });
  }


  render() {
    
    return (
      <>
        <div id="closeBtn" onClick={this._handleArrowClick}>
          { this.state.arrowClicked ? 
            <span
              onMouseEnter={this._handleCloseMouseEnter}
              onMouseLeave={this._handleCloseMouseLeave} 
              className={this.state.closeMouseEnter ? 
                              'mouseEnter' 
                              : ''}>
              <i className="fa fa-close"></i>
            </span>
           : ""}
        </div>
        <div onClick={this._handleArrowClick}>
          { this.state.arrowClicked ?
            <div id="youtube">
              <iframe className="indazooTitle" title="indazooTitle" width="100%" height="100%" src="https://www.youtube.com/embed/GWOcIVZ0K1c" frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen>
              </iframe>
            </div>
          : <a href="#">
              <i className="material-icons" id="playBtn">
                <span onMouseEnter={this._handleArrowMouseEnter} 
                      onMouseLeave={this._handleArrowMouseLeave} 
                      className={this.state.arrowMouseEnter ? 'mouseEnter' : ''}>
                  play_arrow
                </span>
              </i>
            </a>
          }
        </div>
      </>
    )
  }
}

class AppPicture extends PureComponent {
  constructor(props, context) {
    super(props, context);

    this.handleSelect = this.handleSelect.bind(this);

    this.state = {
      index: 0,
      direction: null,
    };
  }

  handleSelect(selectedIndex, e) {
    this.setState({
      index: selectedIndex,
      direction: e.direction,
      fade: true
    });
  }

  render() {
    const { index, direction, fade } = this.state;

    return (
      <Carousel
        activeIndex={index}
        direction={direction}
        onSelect={this.handleSelect}
        fade={fade}
      >
        <Carousel.Item>
          <img
            className="d-block w-100 carouselImg"
            src={indexFirstImg}
            alt="First slide"
          />
          {/* <Carousel.Caption>
            <h3>First slide label</h3>
            <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
          </Carousel.Caption> */}
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100 carouselImg"
            src={indexSecondImg}
            alt="Second slide"
          />

          {/* <Carousel.Caption>
            <h3>Second slide label</h3>
            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
          </Carousel.Caption> */}
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100 carouselImg"
            src={indexThirdImg}
            alt="Third slide"
          />

          {/* <Carousel.Caption>
            <h3>Third slide label</h3>
            <p>
              Praesent commodo cursus magna, vel scelerisque nisl consectetur.
            </p>
          </Carousel.Caption> */}
        </Carousel.Item>
      </Carousel>
    );
  }
}

